@import '../../helpers/colors';
.footer{
    background: $button;
    .footer_block{
        max-width: 1920px;
        margin: 0 auto;
        @media only screen and (max-width: 1440px) {
            //height: auto;
        }
        .form_block{
            width: max-content;
            margin: 0 auto;
            text-align: center;
            .caption{
                font-size: 32px;
                line-height: 48px;
                text-align: center;
                letter-spacing: 1px;
                color: $white;
                padding-top: 170px;
                @media only screen and (max-width: 850px) {
                    padding-top: 50px;
                }
            }
            .input_block{
                position: relative;
                margin-top: 16px;
                .input{
                    width: 325px;
                    height: 60px;
                    background: $white;
                    border-radius: 8px;
                    border: none;
                    outline: none;
                    font-size: 16px;
                    line-height: 24px;
                    color: $input;
                    padding-left: 55px;
                    @media only screen and (max-width: 430px) {
                        width: 270px;
                    }
                }
            }
            .name{
                margin-top: 65px;

                &:before{
                    background-image: url(../../assets/img/footer/footer-name.svg);
                }
            }
            .border_red{
                border: 2px solid red;
                border-radius: 10px;
            }
            .name:before,
            .email:before,
            .company:before{
                content: '';
                width: 17px;
                height: 18px;
                position: absolute;
                left: 21px;
                top: 21px;
            }
            .email:before{
                width: 20px;
                height: 16px;
                background-image: url(../../assets/img/footer/footer-email.svg);
            }
            .company:before{
                width: 24px;
                height: 24px;
                background-image: url(../../assets/img/footer/footer-company.svg);
            }
            .textarea_block{
                margin-top: 16px;
                .textarea_text{
                    width: 340px;
                    height: 180px;
                    background: $white;
                    border: none;
                    border-radius: 8px;
                    outline: none;
                    color: $input;
                    font-size: 16px;
                    line-height: 24px;
                    padding: 20px 18px;
                    resize: none;
                    @media only screen and (max-width: 430px) {
                        width: 285px;
                    }
                }
            }
            .button{
                width: 380px;
                height: 60px;
                background: $footerButton;
                border-radius: 8px;
                font-size: 20px;
                text-align: center;
                color: $white;
                font-family: 'FormularMedium', sans-serif;
                line-height: 58px;
                cursor: pointer;
                margin-top: 20px;
                @media only screen and (max-width: 430px) {
                    width: 325px;
                }
            }
        }
        .footer_info{
            margin: 160px 20px 0 20px;
            font-size: 16px;
            line-height: 24px;
            color: $white;
            padding-bottom: 25px;
            text-align: center;
            opacity: 0.7;
        }
    }
}